@import url("https://fonts.googleapis.com/css2?family=Varela+Round&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Varela Round", sans-serif;
}
body {
  background: hsla(181, 71%, 23%, 1);

  background: linear-gradient(
    0deg,
    hsla(181, 71%, 23%, 1) 0%,
    hsla(180, 78%, 2%, 1) 100%
  );

  background: -moz-linear-gradient(
    0deg,
    hsla(181, 71%, 23%, 1) 0%,
    hsla(180, 78%, 2%, 1) 100%
  );

  background: -webkit-linear-gradient(
    0deg,
    hsla(181, 71%, 23%, 1) 0%,
    hsla(180, 78%, 2%, 1) 100%
  );

  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#116466", endColorstr="#010808", GradientType=1 );
}
.logo-img {
  width: 120px;
  padding-top: 20px;
  margin-left: 35px;
}

.question {
  padding-top: 30px;
  width: 100%;
  height: 150px;
  text-align: center;
  font-size: 40px;
  color: #d1e8e2;
}

.options-list {
  height: 200px;
  text-align: center;
}

.option {
  padding: 4px 0;
  margin-top: 20px;
  height: 50px;
  font-size: 30px;
  background-color: #ffcb9a;
  color: #2c3531;
  margin-left: 290px;
  margin-right: 290px;
  border-radius: 30px;
  list-style: none;
}
.option:hover {
  transform: scale(1.05);
  background-color: #062e1f;
  box-shadow: inset;
  color: #d1e8e2;
  transition-duration: 0.3s;
  cursor: pointer;
}

.welcome {
  font-size: 130px;
  padding-top: 0;
  margin-top: 180px;
  font-family: "Poppins", sans-serif;
  margin-left: 65px;
  color: rgb(115, 250, 74);
  position: relative;
}

.welcome-img {
  width: 400px;
  height: auto;
  top: -5px;
  right: 70px;
  position: absolute;
}
.btn {
  width: 120px;
  height: 50px;
  border: none;
  border-radius: 20px;
  display: block;
  margin-top: 100px;
  margin-left: 1100px;
  cursor: pointer;
  background-color: rgb(255, 255, 18);
  color: #2c3531;
  font-size: 20px;
  font-weight: bold;
}
.btn:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
}

.result {
  font-size: 50px;
  margin-top: 200px;
  color: #ffcb9a;
  font-weight: bold;
  text-align: center;
}
